.canvas-list-wrapper {
  width: 134px;

  .scroll-area {
    height: calc(100vh - 64px);
  }

  .scroll-content {
    padding: 10px 0;
  }

  .canvas-list {
    list-style: none;
    margin:0;
    padding:0;

    .item {
      position: relative;
      margin: 5px 0;
      border: 3px solid rgb(133, 133, 133);

      &.active {
        border-color: red;
      }

      .index {
        position: absolute;
        padding: 10px;
        top: 0;
        left: 0;
        color: white;

        &.active {
          color: red;
        }
      }

      .buttons-container {
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;

        .button {
          color: white;

          &.disabled {
            opacity: 0.5;
            cursor: not-allowed;
            pointer-events: auto;
          }
        }

        .delete-button {
          position: absolute;
          top: 0;
          right: 0;
        }

        .copy-button {
          position: absolute;
          right: 0;
          bottom: 0;
        }

        .move-button {
          position: absolute;
          bottom: 0;
          left: 0;
          cursor: move;
        }
      }

      &:hover {
        border-color: #eee;

        &.active {
          border-color: red;
        }

        .buttons-container {
          display: block;
        }
      }
    }

    .empty-item {
      height: 10px;
    }

    .placeholder {
      margin: 5px 0;
      padding: 10px;
      border: 3px dotted rgb(133, 133, 133);

      &:before {
        content: "Drop here";
        color: white;
      }
    }
  }

  .add-button {
    width: 100%;
    box-sizing: border-box;
  }
}
