.color-picker-wrapper {
  position: relative;
  height: 90px;
  margin-top: 10px;
  margin-left: 50px;

  .color {
    position: absolute;
    width: 60px;
    height: 60px;
    box-sizing: border-box;
    border: 3px solid #656565;
    cursor: pointer;

    &:hover {
      border-color: red;
    }
  }

  .second-color {
    top: 30px;
    left: 30px;
  }

  .swap-button {
    position: absolute;
    bottom: -10px;
    left: -10px;
  }

  .swap-icon {
    transform: rotate(45deg);
    color: white;
  }
}
