.main-container {
  width: 100%;
  background-color: rgb(199, 194, 194);

  .screenshots {
    display: flex;
    flex-direction: column;

    .screenshot {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      padding: 50px 50px 0px;
    }

    .screenshot_description {
      padding: 0 50px;
      font-size: 16px;
      font-weight: 500;

      .keyboard-shortcuts {
        display: block;
        margin-top: 20px;
        font-size: 20px;

        &-description li {
          list-style: none;
          font-size: 15px;
        }
      }
    }
  }

  .container-gifs {
    display: flex;
    flex-direction: column;
    padding: 50px 50px 0;
    width: 400px;
    height: 200px;

    &_title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 28px;
      font-weight: 700;
      margin-bottom: 30px;
    }

    .example-gifs {
      display: flex;
      justify-content: space-around;
      width: 100%;
      height: 100%;

      .container-gif {
        width: 100px;
        height: 100px;

        .gif {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  .container-functionality {
    padding: 50px 50px;

    &_title {
      font-size: 25px;
    }

    &_subtitle {
      margin-top: 20px;
      font-size: 20px;
    }

    .list-tools {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      flex-wrap: wrap;
      box-sizing: border-box;
      list-style-type: none;

      .tool-name {
        display: flex;
        align-items: center;
        margin: 20px;
        font-size: 18px;
      }

      li {
        display: flex;
        margin-top: 15px;
      }
    }

    .frame {
      display: flex;
      margin-top: 30px;

      &_imgs {
        display: flex;
        flex-direction: column;

        .img_frame {
          margin-bottom: 20px;
        }
      }

      &_description {
        margin-left: 15px;
        font-size: 18px;
      }
    }
  }

  .layer-managment-title{
    padding: 50px 50px;
  }

  .container-layer-managment {
    display: flex;
    flex-direction: row;

    .cont-layer-managment_img {
      width: 300px;
      background-color: red;

      .layer-managment_img {
        width: 100%;
        height: 100%;
      }
    }

    .layer-managment_description {
      margin-left: 40px;
      font-size: 20px;
    }
  }


  .developer {
    width: 80%;
    height: 500px;
    margin: 70px auto 70px;
    background-color: rgb(27, 85, 85);

    .developer-title {
      font-size: 40px;
      margin-bottom: 40px;
      text-align: center;
      color: white;
    }

    .photo-developer {
      display: block;
      margin: 0 auto;
      border-radius: 100%;
    }

    .about-developer {
      font-size: 40px;
      margin-top: 40px;
      text-align: center;
      color: white;
    }
  }

  .footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70px;
    background-color: black;
    color: white;

    .nameAuthor {
      margin-left: 30px;

      .linkRSS {
        margin: 0 10px;
        text-decoration: none;
        color: yellow;
      }
    }

    .contacts {
      display: flex;
      margin-right: 30px;
    }
  }
}
