.export-panel-wrapper {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  max-width: 300px;

  .button {
    margin: 10px;
    min-height: 37px;

    &.disabled {
      background-color: rgba(255, 255, 255, 0.5);
    }
  }

  .button-wrapper {
    position: relative;
  }

  .button-progress {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -12px;
    margin-left: -12px;
  }
}
